import startSearch from './startSearch';

export default function translate(request){
  document.getElementById('Showing_results').classList.remove('hide');
  fetch(request)
  .then(function(result){
    return result.json();
  }).then(function(data){
    let title = data.text[0];
    let request = new Request('https://www.omdbapi.com/?s='+title+'&apikey=b43b13bc');
    startSearch(request);
  });

}
