import NoResultsFor from './messages';
import Swiper from '../../node_modules/swiper/js/swiper.min.js';


export default function startSearch(request){
fetch(request)
.then(function(result){
  if(result.status >= 400){
    let error = new Error(result.statusText);
    alert('Error: ' + error);
  }
  document.getElementById('wait').classList.remove('hide');
  return result.json();
  })
.then(function(data){
  if(data.Response === 'False'){
    NoResultsFor();
  }
  if(document.getElementById('search_result').childNodes.length !== 1) {
    let myNode = document.getElementById('search_result');
    while (myNode.firstChild) {
      myNode.removeChild(myNode.firstChild);
    }
  }
  let searchEl = document.getElementById('search_result');
  let len = data.Search.length;
  for(let i = 0; i < len; i++){
    let movieContainer = document.createElement('div');
    movieContainer.className = 'search-result--item swiper-slide';
    let yearEl = document.createElement('div');
    yearEl.innerText = data.Search[i].Year;
    let typeEl = document.createElement('div');
    typeEl.innerText = data.Search[i].Type;
    let posterEl = document.createElement('img');
    posterEl.src = data.Search[i].Poster;
    movieContainer.appendChild(posterEl);
    let idMovie = data.Search[i].imdbID;
    fetch('https://www.omdbapi.com/?i=' + idMovie + '&apikey=b43b13bc')  //7e6fe831
    .then(response => response.json())
    .then(function(data){
      let title = document.createElement('a');
      title.innerText = data.Title;
      title.setAttribute('href', 'https://www.imdb.com/title/' + data.imdbID + '/');
      let rating = document.createElement('div');
      rating.innerText = data.imdbRating;
      movieContainer.appendChild(title);
      movieContainer.appendChild(rating);
      movieContainer.appendChild(yearEl);
      movieContainer.appendChild(typeEl);
    }
    );
    searchEl.appendChild(movieContainer);
  }
  document.getElementById('wait').classList.add('hide');
  new Swiper(document.querySelector('.swiper-container'), {
    breakpoints: {
      320: {
        slidesPerView: 1,
        spaceBetween: 30
      },

      470: {
        slidesPerView: 2,
        spaceBetween: 20
      },

      730: {
        slidesPerView: 3,
        spaceBetween: 20
      },

      1500: {
        slidesPerView: 4,
        spaceBetween: 40
      }
    },
    slidesPerColumn: 1,
    slidesPerColumnFill: 'row',
    pagination: {
      el: '.swiper-pagination',
      clickable: true,
    },
  });
});
}

