import render from './view/render';
import StartPageMovie from './view/StartMovie';
import languageСheck from './languageСheck';

render();
StartPageMovie();

document.getElementById('clearButton').onclick = function(e) {
  event.preventDefault();
  document.getElementById('title_text').value = '';
};

document.getElementById('search_btn').addEventListener('click', function(){
  document.getElementById('No_results').classList.add('hide');
  document.getElementById('Showing_results').classList.add('hide');
  let title = document.getElementById('title_text').value;
  languageСheck(title);
});

document.onkeydown = (event) => {
  if (event.keyCode === 13) {
    event.preventDefault();
    document.getElementById('No_results').classList.add('hide');
    document.getElementById('Showing_results').classList.add('hide');
    let title = document.getElementById('title_text').value;
    languageСheck(title);
  }
};



